.App {
  text-align: center;
  margin: 0;
}

.videoTag{
  position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    transform: translateX(-50%) translateY(-50%);
    background: src="./photo.jpg" no-repeat;
    background-repeat:no-repeat;
    background-size:100% 100%;
    transition: 1s opacity;

}
.content {
  font-family: Agenda-Light, Agenda Light, Agenda, Arial Narrow, sans-serif;
  font-weight:100; 
  background: rgba(238, 113, 175, 0.3);
  color: white;
  padding: 2rem;
  width: 100%;
  font-size: 1.2rem;
  margin-top: 235px;
}

@font-face {
  font-family: 'Citrica Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Citrica Regular'), url('Citrica-Regular.ttf') format('woff');
}

h1 {
  font-family: 'Citrica Regular';
  font-weight: 700;
  color: #0ccac4;
  text-transform: uppercase;
  font-weight: bolder;
}