.btn {
    box-sizing: border-box;
    appearance: none;
    background-color: transparent;
    border: 2px solid #0ccac4;
    border-radius: 0.6em;
    color: white;
    cursor: pointer;
    align-self: center;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
  
.btn:hover, :focus {
      color: rgb(180, 121, 121);
      outline: 0;
      transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
      box-shadow: 0 0 40px 40px #0ccac4 inset;
}

.result {
    margin: 50px;
}